/* full screen icon */
.gantt-fullscreen {
    position: absolute;
    bottom: 20px;
    right: 20px;
    width: 30px;
    height: 30px;
    padding: 2px;
    font-size: 32px;
    background: transparent;
    cursor: pointer;
    opacity: 0.5;
    text-align: center;
    /*  0.5  */
    -webkit-transition: background-color 0.5s, opacity 0.5s;
    transition: background-color 0.5s, opacity 0.5s;
}

.gantt-fullscreen:hover {
    background: rgba(150, 150, 150, 0.5);
    opacity: 1;
}

.weekend {
    background-color: rgba(0, 199, 181, .2) !important;
}

/* Resizer  */
.gantt-resizer {
    position: absolute;
    right: 0;
    top: 0;
    width: 1px;
    height: 100%;
    /*background-color: #f0f;*/
    cursor: col-resize;
}

/* marker  */
.holiday {
    background-color: rgba(237, 125, 49, .2);
}

.holiday .gantt_marker_content {
    color: #0e0e0e;
}

.today .gantt_marker_content {
    color: #0e0e0e;
    opacity: 1;
}

